

















import { Component, Vue } from "vue-property-decorator";
import { TimelineMax, TweenMax } from "gsap";
import Card from "@/app/components/common/Card.vue";
import Hobbies from "@/app/components/blocks/other/Hobbies.vue";
import ContactMe from "@/app/components/blocks/other/ContactMe.vue";

@Component({
  components: {
    Card,
    Hobbies,
    ContactMe,
  },
})
export default class Others extends Vue {
  mounted() {
    const timeline = new TimelineMax();

    timeline.add([
      TweenMax.to(".hobbies", 500, { opacity: 1 }),
      TweenMax.to(".hobbies", 500, { opacity: 0, delay: 400, display: "none" }),
      TweenMax.to(".contact-me", 500, { opacity: 1, delay: 400 }),
      TweenMax.to("#s4-title", 500, { color: "#01131d", delay: 400 }),
      TweenMax.to("#scroll-top", 500, {
        opacity: 1,
        cursor: "pointer",
        delay: 400,
      }),
    ]);

    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#S4",
        triggerHook: 0.01,
        duration: 2000,
      })
      .setPin("#S4")
      .setTween(timeline);

    this.$scrollmagic.addScene(scene);
  }

  toTop() {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }
}
