





















import { Component, Vue } from "vue-property-decorator";
import { Power0, TimelineMax, TweenMax } from "gsap";
import FESkills from "@/app/components/blocks/skills/FESkills.vue";
import BESkills from "@/app/components/blocks/skills/BESkills.vue";
import SoftSkills from "@/app/components/blocks/skills/SoftSkills.vue";

// Tech Skill
const SKILL_DURATION = 500;
const FE_DELAY = 800;
const BE_DELAY = 2300;

// Soft Skill
const OPAC_DELAY = 4050;
const OPAC_DURATION = 100;

const SWING_DELAY = 4000;
const SWING_DURATION = 3000;

@Component({
  components: {
    FESkills,
    BESkills,
    SoftSkills,
  },
})
export default class Skills extends Vue {
  mounted() {
    const clientWidth = window.innerWidth;
    const clientHeight = window.innerHeight;

    const bezierPath = {
      left: {
        curviness: 1.25,
        autoRotate: true,
        values: [
          { x: 0, y: -clientHeight / 18 },
          { x: clientWidth / 10, y: clientHeight / 90 },
          { x: clientWidth / 1.4, y: -clientHeight / 4.5 },
          { x: clientWidth / 1.2, y: clientHeight / 8 },
        ],
      },
      right: {
        curviness: 1.25,
        autoRotate: -180,
        values: [
          { x: 0, y: -clientHeight / 18 },
          { x: -clientWidth / 10, y: clientHeight / 90 },
          { x: -clientWidth / 1.4, y: -clientHeight / 4.5 },
          { x: -clientWidth / 1.2, y: clientHeight / 8 },
        ],
      },
    };
    const timeline = new TimelineMax();

    timeline.add([
      // FE skills animations
      TweenMax.to(".fe", SKILL_DURATION, { opacity: 1, delay: 400 }),
      TweenMax.to("#vue-bar", SKILL_DURATION, {
        width: "80%",
        delay: FE_DELAY,
      }),
      TweenMax.to("#react-bar", SKILL_DURATION, {
        width: "50%",
        delay: FE_DELAY,
      }),
      TweenMax.to("#angular-bar", SKILL_DURATION, {
        width: "30%",
        delay: FE_DELAY,
      }),
      TweenMax.to(".slide-menu", SKILL_DURATION, {
        x: "-104vw",
        ease: Power0.easeNone,
        delay: 1500,
      }),
      // BE skills animations
      TweenMax.to(".be", SKILL_DURATION, { opacity: 1, delay: 1900 }),
      TweenMax.to("#spring-bar", SKILL_DURATION, {
        width: "30%",
        delay: BE_DELAY,
      }),
      TweenMax.to("#quarkus-bar", SKILL_DURATION, {
        width: "30%",
        delay: BE_DELAY,
      }),
      TweenMax.to("#flask-bar", SKILL_DURATION, {
        width: "10%",
        delay: BE_DELAY,
      }),
      TweenMax.to("#asp-bar", SKILL_DURATION, { width: "5%", delay: BE_DELAY }),
      TweenMax.to(".slide-menu", SKILL_DURATION, {
        x: "-208vw",
        ease: Power0.easeNone,
        delay: 3000,
      }),
      // Soft skills animations
      TweenMax.to(".sk", SKILL_DURATION, { opacity: 1, delay: 3400 }),
      TweenMax.to("#sk-1", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-1", SWING_DURATION, {
        bezier: bezierPath.left,
        delay: SWING_DELAY,
      }),
      TweenMax.to("#sk-2", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-2", SWING_DURATION, {
        bezier: bezierPath.left,
        delay: SWING_DELAY,
      }),
      TweenMax.to("#sk-3", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-3", SWING_DURATION, {
        bezier: bezierPath.left,
        delay: SWING_DELAY,
      }),
      TweenMax.to("#sk-4", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-4", SWING_DURATION, {
        bezier: bezierPath.right,
        delay: SWING_DELAY,
      }),
      TweenMax.to("#sk-5", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-5", SWING_DURATION, {
        bezier: bezierPath.right,
        delay: SWING_DELAY,
      }),
      TweenMax.to("#sk-6", OPAC_DURATION, {
        opacity: 1,
        delay: OPAC_DELAY,
      }),
      TweenMax.to("#sk-6", SWING_DURATION, {
        bezier: bezierPath.right,
        delay: SWING_DELAY,
      }),
      TweenMax.to(".sk", 1000, { opacity: 0, display: "none", delay: 6000 }),
    ]);

    const scene = this.$scrollmagic
      .scene({
        triggerElement: "#S3",
        triggerHook: 0.01,
        duration: SWING_DELAY,
      })
      .setPin("#S3")
      .setTween(timeline);

    this.$scrollmagic.addScene(scene);
  }

  get smallScreen() {
    return this.$mediaQuery.is("smDown");
  }
}
